<template>
    <div>
        <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <!--  -->
        <el-card class="box-card">
              <el-row :gutter="20">
               <el-col :span="2" v-if="this.identity !==2 || this.identity !== 3">
                  <div class="schoolSet"></div>
               </el-col>
               <el-col :span="5" v-if="this.identity !==2 || this.identity !== 3">
                 <el-autocomplete
                 width="100%"
                  class="inline-input"
                  v-model="schoolSelected"
                  clearable
                  :fetch-suggestions="querySearchSchool"
                  placeholder=""
                  @select="handleSelectSchool"
                  @change="handleSchoolChange"
                  ></el-autocomplete>
                 </el-col>
                 <el-col :span="2" v-if="this.identity !== 3">
                    <div class="schoolSet"></div>
                 </el-col>
                 <el-col :span="4"  v-if="this.identity ==  2|| this.identity ==  1 || this.identity == 5">
                   <el-autocomplete
                    class="inline-input"
                    v-model="className"
                    clearable
                    :fetch-suggestions="querySearchClass"
                    placeholder=""
                    @select="handleSelectClass"
                    ></el-autocomplete>
                  </el-col>
                  <el-col :span="2">
                   <el-button type="primary" @click="getRecodRight"></el-button>
                  </el-col>
                <el-col :span="2">
                   <el-button type="primary" @click="addClass"></el-button>
                </el-col>
                <el-col :span="2">
                   <el-button type="primary" @click="upperClass"></el-button>
                </el-col>
            </el-row>
             <!--  -->
            <el-table border  :data="content" stripe style="width: 100%"  v-show="!this.searchClassList.length">
              <el-table-column type="index"></el-table-column>
              <el-table-column label="" prop="schoolName"></el-table-column>
              <el-table-column label="" prop="className"></el-table-column>
              <el-table-column label="" prop="volume"></el-table-column>
              <el-table-column label="" prop="roomLength"></el-table-column>
              <el-table-column label="" prop="roomWidth"></el-table-column>
              <el-table-column label="" prop="bbLength"></el-table-column>
              <el-table-column label="" prop="experiment"></el-table-column>
              <el-table-column label="" prop="description"></el-table-column>
              <el-table-column label="">
                  <template slot-scope="scope">
                      <el-button type="primary" size="middle" icon="el-icon-edit"  @click="showClassEditDialog(scope.row.id)" ></el-button>
                  </template>
              </el-table-column>
               <el-table-column label="">
                  <template slot-scope="scope">
                      <el-button type="danger"  size="middle" icon="el-icon-delete" @click="removeClassById(scope.row.id)"></el-button>
                  </template>
              </el-table-column>
            </el-table>
            <!--  -->
            <el-table border  :data="this.searchClassList" stripe style="width: 100%" v-show="this.searchClassList.length">
              <el-table-column type="index"></el-table-column>
              <el-table-column label="" prop="schoolName"></el-table-column>
              <el-table-column label="" prop="className"></el-table-column>
              <el-table-column label="" prop="volume"></el-table-column>
               <el-table-column label="" prop="roomLength"></el-table-column>
              <el-table-column label="" prop="roomWidth"></el-table-column>
              <el-table-column label="" prop="bbLength"></el-table-column>
              <el-table-column label="" prop="experiment"></el-table-column>
              <el-table-column label="" prop="description"></el-table-column>
              <el-table-column label="">
                  <template slot-scope="scope">
                      <el-button type="primary" size="middle" icon="el-icon-edit"  @click="showClassEditDialog(scope.row.id)" ></el-button>
                  </template>
              </el-table-column>
               <el-table-column label="">
                  <template slot-scope="scope">
                      <el-button type="danger"  size="middle" icon="el-icon-delete" @click="removeClassById(scope.row.id)"></el-button>
                  </template>
              </el-table-column>
            </el-table>

           <!--  -->
           <el-pagination
             v-show="!this.searchResult"
             background
             :current-page="this.number"
             @current-change="handleCurrentChange"
             layout="prev, pager, next"
             :page-size ="this.size"
             :total="this.totalElements">
           </el-pagination>
           <el-pagination
             v-if="this.searchResult"
             background
             :current-page="this.number"
             @current-change="handleSearchCurrentChange"
             layout="prev, pager, next"
             :page-size ="this.size"
             :total="this.totalElements">
           </el-pagination>
        </el-card>
        <!--  -->
        <el-dialog title="" ref="addClassRef" :visible.sync="addClassVisible" width="50%" :before-close="handleClose">
            <el-form :model="addClassForm" :rules="addClassRules" ref="addClassRef" label-width="120px" class="demo-ruleForm">
                <el-form-item label="" prop="schoolName">
                    <el-cascader v-model="addClassForm.schoolName"  :options="schoolList" :props ="cateProps" @change="handleChange">
                    </el-cascader>
                  <!--  <el-select v-model="addClassForm.schoolName" placeholder="" >
                        <el-option
                          v-for="item in school"
                          :key="item.id"
                          :label="item.name"
                          :value="item.id">
                        </el-option>
                      </el-select> -->
                </el-form-item>
                <el-form-item label="" prop="className">
                    <el-input v-model="addClassForm.className" clearable></el-input>
                </el-form-item>
                <el-form-item label="()" prop="roomLength">
                    <el-input v-model="addClassForm.roomLength" clearable></el-input>
                </el-form-item>
                <el-form-item label="()" prop="roomWidth">
                    <el-input v-model="addClassForm.roomWidth" clearable></el-input>
                </el-form-item>
                <el-form-item label="()" prop="bbLength">
                    <el-input v-model="addClassForm.bbLength" clearable></el-input>
                </el-form-item>
                <el-form-item label="" prop="volume">
                    <el-input v-model.number="addClassForm.volume" clearable></el-input>
                </el-form-item>

                <el-form-item label="" prop="experiment">
                    <el-radio-group  size="medium"  v-model="addClassForm.experiment">
                    <el-radio border  label="1"></el-radio>
                    <el-radio border label="0"></el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item label="" prop="description">
                    <el-input v-model="addClassForm.description"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button@click=" handleClose"></el-button>
                <el-button type="primary" @click=" sumitClass"> </el-button>
            </span>
        </el-dialog>
        <!--  -->
        <el-dialog title="" :visible.sync="editVisible" width="50%" @close="editDialogClosed">
            <el-form :model="editClassForm" :rules="editClassRules" ref="editClassRef" label-width="120px">
                <!-- <el-form-item label="" prop="schoolName" >
                    <el-cascader v-model="selectedOptions"  :options="school" :props ="cateProps" @change="handleChange">
                    </el-cascader>
                </el-form-item> -->
               <el-form-item label=""  >
                    <el-select v-model="editClassForm.schoolId" placeholder=""   @change="handleEditChange">
                      <el-option
                        v-for="item in school"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                      </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" prop="className">
                    <el-input v-model="editClassForm.className" clearable></el-input>
                </el-form-item>
                <el-form-item label="()" prop="roomLength">
                    <el-input v-model="editClassForm.roomLength" clearable></el-input>
                </el-form-item>
                <el-form-item label="()" prop="roomWidth">
                    <el-input v-model="editClassForm.roomWidth" clearable></el-input>
                </el-form-item>
                <el-form-item label="()" prop="bbLength">
                    <el-input v-model="editClassForm.bbLength" clearable></el-input>
                </el-form-item>
                <el-form-item label="" prop="volume">
                    <el-input v-model.number="editClassForm.volume" clearable></el-input>
                </el-form-item>
                <el-form-item label="" prop="experiment">
                    <!-- <el-radio-group size="medium" > -->
                    <el-radio border  v-model="editClassForm.experiment" :label="1" ></el-radio>
                    <el-radio border  v-model="editClassForm.experiment"  :label="0"></el-radio>
                    <!-- </el-radio-group> -->
                </el-form-item>
                <el-form-item label="" prop="description">
                    <el-input v-model="editClassForm.description"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="editVisible = false"> </el-button>
                <el-button type="primary" @click="editClassInfo"> </el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
import axios from 'axios'
export default {
    created() {
        this.token = window.sessionStorage.getItem('token');
        let user = window.sessionStorage.getItem('token');
        this.identity = user.split('-') [1];
        this.fondId = user.split('-')[2];
        this.getSchoolList();
         if(this.identity == 1) {  // admin
           this.type = '';
           this.getClassList(this.type, 1);
         }else if(this.identity == 2) {   //2 
           this.type = 'school';
           this.getSchoolClasses(this.fondId);
           this.getClassList(this.type,1);
        }else if(this.identity == 3) {   // 
          this.type = "class";
          this.id= this.fondId;
          this.getClassList(this.type,1);
        }else {
          this.type = '';
          this.getSchoolClasses(this.fondId);
          this.getClassList(this.type,1);
        }
    },
    data() {
        var valiNumberPass1 = (rule, value, callback) => {//
            let reg = /^(([1-9]{1}\d*)|(0{1}))(\.\d{1,2})?$/g;
            if (value === '') {
                callback(new Error(''));
            } else if (!reg.test(value)) {
                callback(new Error(''));
            } else {
                callback();
            }
        };
        return {
          pageSize: 5,
          size: 10,
          total:0,
          content: [],
          schoolId: '',
          classId: '',
          schoolSelected: '', // 
          className: '',
          totalElements: 0,
          number: 1,
          searchResult: false,
          searchPage: 1,
          token: '',
          addClassVisible: false,
          editVisible: false,
          school: [],
          value: '',
          schoolId: '',
          schoolList: [],
          classList: [],
          classesList: [],
          labelNum1: 1,
          labelNum2: 0,
          query: '',
          searchClassList: [],
          addClassForm: {
              className:'',
              roomLength: '',
              roomWidth: '',
              bbLength: '',
              volume: '',
              description: '',
              experiment: '',
              schoolName: ''
          },
          addClassRules: {
              schoolName:  { required: true,  message: '', trigger: 'blur' },
              className:  { required: true,  message: '', trigger: 'blur' },
              roomLength:  { required: true, validator: valiNumberPass1, message: '', trigger: 'blur'},
              roomWidth:  { required: true,  validator: valiNumberPass1, message: '', trigger: 'blur' },
              bbLength:  { required: true,  validator: valiNumberPass1,message: '', trigger: 'blur' },
              volume:  { required: true, type:'number', message: '', trigger: 'blur' },
              experiment: {required: true, message: '', trigger: 'blur'}
          },
          cateProps: {
              label: 'name', //
              value: 'id', // 
              children: 'children' //
          },
          page: 1,
          classList: [],
          currentPage: 1,
          pageSize: 5,
          total: 0,
          selectedOptions: [],
          editClassForm: {
              className:'',
              roomLength: '',
              roomWidth: '',
              bbLength: '',
              volume: '',
              description: '',
              experiment: '',
              schoolName: '',
              schoolId: ''
          },
          editClassRules: {
              schoolName:  { required: true, message: '', trigger: 'blur' },
              classNameame:  { required: true, message: '', trigger: 'blur' },
              roomLength:  { required: true, validator: valiNumberPass1, message: '', trigger: 'blur'},
              roomWidth:  { required: true,  validator: valiNumberPass1, message: '', trigger: 'blur' },
              bbLength:  { required: true,  validator: valiNumberPass1, message: '', trigger: 'blur' },
              volume:  { required: true, type:'number', message: '', trigger: 'blur' },
              experiment: {required: true, message: '', trigger: 'blur'}
          }
        }
    },
    methods: {
      handleEditChange(val) {
        this.editClassForm.schoolId = val;
      },
      //
      getRecodRight() {
        this.searchResult = true;
        if(this.type == 'school') {   //
          if(this.className) {
            this.searchPage = 1;
            this.getRecordDirect('class', this.classId); //
          }else {
            this.getClassList(this.type, 1)
            this.searchResult = false;
          }
        }else if(this.type == 'class') {  //

          // if(this.student) {
          //    this.getSearchRecordDirect('student', this.studentId)
          // }else {
          //   this.getStudentList(this.type, 1);
          // }
        }else {
        //
          if(this.schoolSelected && this.className) {
            this.searchPage = 1;
            this.getRecordDirect('class', this.classId); //
          }else if(this.schoolSelected&& this.className == '') {
            this.searchPage = 1;
            this.getRecordDirect('school', this.schoolId); //
          }else if(this.schoolSelected == ''&& this.className) {
            this.$notify({
              title: '',
              duration: 1000,
              message: '',
              type: 'warning'
            });
          }
          else {
             this.getClassList(this.type, 1)
             this.searchResult = false;
          }
        }
      },
      //  
      getRecordDirect(type,id) {
        // if(!this.searchResult) {
        //   this.searchPage  = this.page;
        // }
        let param = new FormData();
        param.append('type', type.toString());
        param.append('id', id);
        param.append('token', this.token);
        param.append('page', this.searchPage);
        axios({
          method: 'post',
          data: param,
          url: '/lightspace/classesList'
        }).then(this.handleGetRecordDirSucc.bind(this)).catch(this.handlgGetRecordDirErr.bind(this))
      },
      handleGetRecordDirSucc(res) {
        if(res.data.status == 200) {
          res ? res= res.data.data: '';
          this.content = res.content;
          this.totalElements = res.totalElements;
          this.size = res.size;
          this.number = res.number + 1;
          if(this.content.length) {
            this.content.forEach((item) => {
              item.lastTime = item.date + '\xa0\xa0' + item.time
            })
          }
        }else if(res.data.status == 10211) {
          this.$notify({
            title: '',
            duration: 1000,
            message: res.data.msg,
            type: 'warning'
          });
        }
      },
      //
      handleSearchCurrentChange(val) {
        this.searchPage = val;
        if(this.type == 'school') {
          if(this.className) {
            this.searchPage = 1;
            this.getRecordDirect('class', this.classId); //
          }else {
            this.getClassList(this.type, 1)
            this.searchResult = false;
          }
        }else if(this.type == 'class') {
          this.getRecodRight(this.type, this.searchPage);
        }else {
          //
            if(this.schoolSelected && this.className) {
              this.searchPage = 1;
              this.getRecordDirect('class', this.classId); //
            }else if(this.schoolSelected&& this.className == '') {
              this.getRecordDirect('school', this.schoolId); //
            }else {
               this.getClassList(this.type, 1)
               this.searchResult = false;
            }
        }
      },
      handlgGetRecordDirErr(err) {
        console.log(err)
      },
      handleSchoolChange(val) {
        this.schoolId =  val.id;
      },
      handleSelectSchool(item) {
        this.schoolId = item.id;
        this.classId = '';
        this.className = '';
        this.student = '';
        this.studentId = '';
        this.getClassesList(item.id);
      },
      handleSelectClass(item) {
        this.classId = item.id;
        this.student = '';
        this.studentId = '';
      },
      // 
      querySearchSchool(queryString, cb) {
        var schoolList = this.schoolList;
        var results = queryString ? schoolList.filter(this.createFilter(queryString)) : schoolList;
        //  callback 
        cb(results);
      },
      createFilter(queryString) {
        return (schoolList) => {
          return (schoolList.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
        }
      },
      // 
      querySearchClass(queryString, cb) {
        var classesList = this.classesList;
        var results = queryString ? classesList.filter(this.createFilter(queryString)) : classesList;
        //  callback 
        cb(results);
      },
      createFilter(queryString) {
        return (classesList) => {
          return (classesList.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
        }
      },
      //
      getSchoolList() {
        let param = new URLSearchParams();
         param.append('token', this.token);
         axios({
             method: 'post',
             url: '/lightspace/schoolList',
             data: param
         }).then(this.handleGetSchoolSucc.bind(this))
           .catch(this.handleGetSchoolErr.bind(this))
      },
      handleGetSchoolSucc(res) {
          // console.log(res)
        if(res.data.status === 10204) {
            this.$message.error(res.data.msg);
            this.$router.push('/login');
           } else if(res.data.status == 200) {
               this.schoolList = res.data.data;
               let sc = res.data.data;
               let options = res.data.data;
               options.forEach((item, index) => {
                 this.school.push({
                   value: item.id,
                   label: item.name
                 })
               })
            if(this.identity == 2) {
               let choose = sc.filter((item, index) => {
                 if(item.id == Number(this.fondId)) {
                   return item
                 }
               })

                this.schoolList = choose;
                this.schoolSelected = choose[0].name;
                this.schoolId = choose[0].id;
             }

          }
      },
      handleGetSchoolErr(error) {
         console.log(error)
      },
      //
      getSchoolClasses(id) {
        let param = new FormData();
        param.append('token', this.token);
        param.append('schoolId',id);
        axios({
          method: 'post',
          url: '/lightspace/queryClassesBySchool',
          data: param,
          }).then((res) => {
            this.classesList = res.data.data;
            this.classesList.forEach((item) => {
             item.value = item.className
            })
          }).catch((err) => {
            console.log(err)
          })
      },
      //
      getClassesList(id) {
        let param = new FormData();
        param.append('token', this.token);
        param.append('schoolId', id)
        axios({
          method: 'post',
          url: '/lightspace/queryClassesBySchool',
          data: param
        }).then(this.handleGetSearchSucc.bind(this)).catch(this.handleGetSearchErr.bind(this))
      },
      handleGetSearchSucc(res) {
        if(res.data.status == 200) {
          this.classesList = res.data.data;
          this.classesList.forEach((item) => {
           item.value = item.className
          })
        }
      },
      handleGetSearchErr(err) {
        console.log(err)
      },
      //
      async  upperClass(){
      const confirmResult = await this.$confirm(', ?', '', {
      confirmButtonText: '',
      cancelButtonText: '',
      type: 'warning'
      }).catch(err => err)
      if(confirmResult !== 'confirm') {
          return this.$message.info('')
      }
        let param = new FormData();
        param.append('token', this.token)
        axios({
          method: 'post',
          data: param,
          url: '/lightspace/elevateClass'
        }).then(this.handleUpperSucc.bind(this)).catch(this.handleUpperErr.bind(this))
      },
      handleUpperSucc(res) {
        //  console.log(res);
         if(res.data.status == 200) {
           this.classList = res.data.data;
           this.$message({
             message: '',
             type: 'success'
           });
         }
      },
      handleUpperErr(err) {
        console.log(err)
      },
     handleQuerySucc(res) {
       if(res.data.status === 10204) {
           this.$message.error(res.data.msg);
           this.$router.push('/login');
       } else if(res.data.status == 10210) {
           this.$message.error(res.data.msg);
           this.getClassList(this.type, this.page);
           this.searchClassList = [];
        }else if(res.data.status == 200) {
           this.$message.success('');
           this.searchClassList = res.data.data;
         }
     },
      handleQueryErr(err) {
          console.log(err)
      },
      //
        handleClose() {
         this.addClassVisible = false;
         this.$refs.addClassRef.resetFields();
         this.addClassForm.description = ''
        },
        addClass() {
            this.addClassVisible = true
        },
        //
        getClassList (type, page)  {
            let param = new URLSearchParams();
            param.append('token' ,this.token);
            param.append('type', type);
            param.append('page', page);
            param.append('id', this.fondId);
            axios({
                method: 'post',
                data: param,
                url: '/lightspace/classesList'
            }).then(this.handleGetClassSucc.bind(this))
            .catch(this.handleGetClassErr.bind(this)
            )
        },
        handleGetClassSucc(res) {

            if(res.status !== 200) return this.$message.error('');
            this.classList = res.data.data;
            if(res.data.status === 10204) {
                this.$message.error(res.data.msg);
                this.$router.push('/login');
               } else if(res.data.status == 200) {
                 res ? res= res.data.data: '';
                 this.content = res.content;
                 this.totalElements = res.totalElements;
                 this.size = res.size;
                 this.number = res.number + 1;
                 this.content.forEach((item, index) => {
                   if(item.experiment == '1') {
                     item.experiment = ''
                   }else{
                      item.experiment = ''
                   }
                 })
              }
        },
        handleGetClassErr(err) {
            console.log(err)
        },
        //
        handleCurrentChange(val) {
          this.page = val;
          if(this.type == 'school') {
            this.getClassList(this.type, this.page);
          }else if(this.type == 'class') {
            this.getClassList(this.type, this.page);
          }else {
             this.getClassList('', this.page);
            // if(this.school && this.className && this.student) {
            //   this.getStuInType('student', this.studentId, this.page); //
            // }else if(this.school && this.className && this.student == '') {
            //   this.getStuInType('class', this.classId, this.page); //
            // }else if(this.school && this.className == '' && this.student == '') {
            //   this.getStuInType('school',this.schoolId, this.page);
            // }else {
            //   this.getClassList('', this.page);
            // }
          }
        },
        // 
        sumitClass() {
            this.$refs.addClassRef.validate((valid) => {
                if(!valid)  return this.$message.error('');
                var param = new URLSearchParams();
                param.append('token', this.token);
                param.append('schoolId',this.schoolId);
                param.append('className',this.addClassForm.className);
                param.append('roomLength',this.addClassForm.roomLength);
                param.append('roomWidth',this.addClassForm.roomWidth);
                param.append('volume',this.addClassForm.volume);
                param.append('bbLength', this.addClassForm.bbLength);
                param.append('description',this.addClassForm.description);
                param.append('experiment',this.addClassForm.experiment);
                axios({
                    method: 'post',
                    data: param,
                    url:'/lightspace/addClasses'
                }).then(this.handleAddClassSucc.bind(this))
                .catch(this.handleAddClassErr.bind(this))
            })
        },
         handleChange(item) {
           this.schoolId = item;
        },
        handleAddClassSucc(res) {
           if(res.data.status == 10204) {
               this.$message.error(res.data.msg);
               this.$router.push('/login');
            }else if(res.data.status == 10207)  {
               this.$message.error(res.data.msg);
                this.addClassForm.className = '';
            }else if(res.data.status == 200) {
              this.$message.success('')
              this.addClassVisible = false;
              this.$refs.addClassRef.resetFields();
              this.addClassForm.description = '';
              this.classList = res.data.data;
            }
        },
        handleAddClassErr(err) {
            console.log(err)
        },
        resetAddClass() {
            this.$refs.addClassRef.resetFields();
        },
        //
        showClassEditDialog (id) {
            let param = new URLSearchParams();
            param.append('id', id);
            param.append('token',this.token)
            axios({
                method: 'post',
                url: '/lightspace/editClasses',
                data: param
            }).then(this.handleEditClassSucc.bind(this))
            .catch(this.handleEditclassErr.bind(this))
        },
        handleEditClassSucc(res) {
            if(res.data.status === 10204) {
                this.$message.error(res.data.msg);
                this.$router.push('/login');
            } else if(res.data.status == 200) {
              this.editClassForm = res.data.data;
              this.selectedOptions[0] = res.data.data.schoolId;
             this.selectedOptions[1] = res.data.data.id;
              this.editVisible = true;
            }
        },
        handleEditclassErr(err) {
            console.log(err)
        },
        //
        editClassInfo() {
            this.$refs.editClassRef.validate((valid) => {
                if(!valid)  return;
                let param = new URLSearchParams();
                param.append('token', this.token);
                param.append('schoolId',this.editClassForm.schoolId);
                param.append('className',this.editClassForm.className);
                param.append('roomLength',this.editClassForm.roomLength);
                param.append('roomWidth',this.editClassForm.roomWidth);
                param.append('volume',this.editClassForm.volume);
                param.append('bbLength', this.editClassForm.bbLength);
                param.append('description',this.editClassForm.description);
                param.append('experiment',this.editClassForm.experiment);
                param.append('id', this.editClassForm.id)
                axios({
                    method: 'post',
                    url: '/lightspace/saveClasses',
                    data: param
                }).then(this.handleEditSaveClassSucc.bind(this))
                .catch(this.handleEditSaveClassErr.bind(this))
                })
        },
         handleEditSaveClassSucc(res) {

             if(res.data.status === 10204) {
                 this.$message.error(res.data.msg);
                 this.$router.push('/login');
             } else if(res.data.status == 200) {
                //
                 this.classlList = res.data.data;
                //
                this.editVisible = false;
                //
                this.$message.success('');

                this.getClassList(this.type, this.page);
             }
        },
        handleEditSaveClassErr(err) {
            console.log(err)
        },
         //
        editDialogClosed() {
            this.$refs.editClassRef.resetFields()
        },
        //
        async removeClassById(id) {
        const confirmResult = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
        }).catch(err => err)
        if(confirmResult !== 'confirm') {
            return this.$message.info('')
        }
        let param = new URLSearchParams();
        param.append('token', this.token);
        param.append('id', id)
        axios({
            method: 'post',
            url: '/lightspace/deleteClasses',
            data: param
        }).then(this.handleDeleteClassSucc.bind(this))
        .catch(this.handleDeleteClassErr.bind(this))
        },
        handleDeleteClassSucc(res) {
          if(res.data.status === 10204) {
              this.$message.error(res.data.msg);
              this.$router.push('/login');
          } else if(res.data.status == 200) {
             this.$message.success('');
             this.classList = res.data.data;
             const totalPage = Math.ceil(this.classList.length / this.pageSize) // 
             this.currentPage = this.currentPage > totalPage ? totalPage : this.currentPage
             this.currentPage = this.currentPage < 1 ? 1 : this.currentPage
          }
        },
        handleDeleteClassErr(err) {
            console.log(err)
        }
    }
}
</script>
<style lang="less" scoped>
</style>



