<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-row :gutter="20">
       <el-col :span="6">
          <el-input placeholder="" v-model="query" clearable @clear="queryShop">
              <el-button slot="append" icon="el-icon-search" @click="queryShop"></el-button>
           </el-input>
       </el-col>
       <el-col :span="6">
             <el-button type="primary" @click="addDialogVisible = true"></el-button>
       </el-col>
       <el-col :span="6">
         <el-button  type="primary" @click="switchShow">{{showFlag}}</el-button>
       </el-col>
      </el-row> 
      <el-table :data="this.content" border  stripe style="width: 100%"  v-show="!this.searchContent.length">
        <el-table-column type="index"></el-table-column>
        <el-table-column label="" width= '150' prop="name"></el-table-column>
        <el-table-column label="" prop="pictures" style="width: 50%;">
          <template slot-scope="scope">
             <img v-for="item in scope.row.pictures" :src="item" class="swiperImg" />
          </template>
         </el-table-column>
        <el-table-column label="" width= '250' prop="details">
         <template slot-scope="scope">
           <img  :src="scope.row.details" class="swiperImg"  style="height: 100px; width: auto;"/>
         </template>
        </el-table-column>
        <el-table-column label="" prop="partnershipName"></el-table-column>
        <el-table-column label="" prop="genTime"></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
              <el-button type="primary" size="middle" icon="el-icon-edit"  @click="showEditDialog(scope.row.id)" ></el-button>
          </template>
        </el-table-column>
        <el-table-column  width= '200' label="">
          <template slot-scope="scope">
             <el-button type="danger"  size="middle" icon="el-icon-delete" @click="removeShopById(scope.row.id)" ></el-button>
          </template>
      </el-table-column>
      </el-table>
      <el-pagination
       v-show="!this.searchContent.length"
        background
        :current-page="this.number"
        @current-change="handleCurrentChange"
        layout="prev, pager, next"
        :page-size ="10"
        :total="this.totalElements">
      </el-pagination>
       <!--  -->
       <el-table :data="this.searchContent" border  stripe style="width: 100%"  v-show="this.searchContent.length">
         <el-table-column type="index"></el-table-column>
         <el-table-column label="" width= '150' prop="name"></el-table-column>
         <el-table-column label="" prop="pictures" style="width: 50%;">
           <template slot-scope="scope">
              <img v-for="item in scope.row.pictures" :src="item" class="swiperImg" />
           </template>
          </el-table-column>
         <el-table-column label="" width= '250' prop="details">
          <template slot-scope="scope">
            <img  :src="scope.row.details" class="swiperImg" />
          </template>
         </el-table-column>
         <el-table-column label="" prop="partnershipName"></el-table-column>
         <el-table-column label="" prop="genTime"></el-table-column>
         <el-table-column label="">
           <template slot-scope="scope">
               <el-button type="primary" size="middle" icon="el-icon-edit"  @click="showEditDialog(scope.row.id)" ></el-button>
           </template>
         </el-table-column>
         <el-table-column  width= '200' label="">
           <template slot-scope="scope">
              <el-button type="danger"  size="middle" icon="el-icon-delete" @click="removeShopById(scope.row.id)" ></el-button>
           </template>
       </el-table-column>
       </el-table>
       <el-pagination
         v-show="this.searchContent.length"
         background
         :current-page="this.searchNumber"
         @current-change="handleSearchCurrentChange"
         layout="prev, pager, next"
         :page-size ="this.searchSize"
         :total="this.searchTotalElements">
       </el-pagination>
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="addDialogVisible" width="50%" :before-close="handleClose">
       <!--  -->
         <el-form  :model="form2" label-width="80px" :rules="addShopRules" ref="addShopFormRef">
          <el-form-item label="" label-width="120px" prop="name">
           <el-input v-model="form2.name" name="names" style="width:360px;"></el-input>
       </el-form-item>
     <el-form-item label-width="120px" label="(6)" prop="addDetail" >
       <!--elementuiupload :before-upload="beforeUpload"> -->
         <el-upload
             ref="upload"
         class="upload-demo"
             v-model="form2.addDetail"
         action="/as"
         :limit= "6"
             multiple
             :http-request="handleUpload"
         :auto-upload="false"
         list-type="picture">
         <el-button size="small" type="primary"></el-button>
           <div slot="tip" class="el-upload__tip">jpg/png500kb</div>
         </el-upload>
      </el-form-item>
         <el-form-item label-width="120px" label="" prop="addSwiper">
       <!--elementuiupload :before-upload="beforeUpload"> -->
       <el-upload
             ref="detailUpload"
         class="upload-demo"
         action="/as"
         :limit= "1"
             multiple
             :http-request="handleDetailUpload"
         :auto-upload="false"
         list-type="picture">
         <el-button size="small" type="primary"></el-button>
           <div slot="tip" class="el-upload__tip">jpg/png500kb</div>
       </el-upload>
     </el-form-item>
        <el-form-item label="" prop='coorparation'>
           <el-select v-model="form2.coorparation" placeholder=""   @change="handleChange">
             <el-option
               v-for="item in options"
               :key="item.value"
               :label="item.label"
               :value="item.value">
             </el-option>
           </el-select>
       </el-form-item>
     <el-form-item style="padding-top:20px;" label-width="120px" >
          <el-button type="primary" @click="onSubmit"></el-button>
      </el-form-item>
    </el-form>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="editDialogVisible" width="50%" :before-close="handleEditClose">
       <!--  -->
         <el-form ref="form2" :model="editForm" label-width="80px">
           <el-form-item label="" label-width="120px">
           <el-input v-model="editForm.name" name="names" style="width:360px;"></el-input>
       </el-form-item>
       <el-form-item label-width="120px" label="(6)" wx:if="this.editForm.pictures.length">
           <!--  -->
           <el-upload
             ref="editPic"
             action="/as"
             :limit = '6'
             :auto-upload = "true"
             list-type="picture-card"
             :file-list = "pictureFile"
             :on-remove="handleRemove"
             :http-request="reuploadPicture"
              style="display: inline-block">
             <i class="el-icon-plus"></i>
           </el-upload>
           </el-form-item>
           <el-form-item label-width="120px" label="">
             <!--  -->
             <el-upload
               ref="editUpload"
               action="/as"
               :limit = '1'
               :auto-upload = "true"
               :http-request="reupload"
               list-type="picture-card"
               :file-list = "detailFile"
               :on-remove="handleRemoveDetail"
               >
               <i class="el-icon-plus"></i>
            </el-upload>
         </el-form-item>
         <el-form-item label="" prop='partnershipId'>
                <el-select v-model="editForm.partnershipId" placeholder=""   @change="handleChange">
                  <el-option
                    v-for="item in options"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                  </el-option>
                </el-select>
            </el-form-item>
          
     <el-form-item style="padding-top:20px;" label-width="120px" >
          <el-button type="primary" @click="onresetSubmit"></el-button>
      </el-form-item>
    </el-form>
    </el-dialog>
  </div>
</template>

<script>
  import axios from 'axios'
  export default {
    created() {
      this.token = window.sessionStorage.getItem('token');
      this.getShopList(1);
       this.getOPtions();
    },
    data() {
      return {
         token: '',
         query: '',
         showFlag: '',
		 options: [],
         addDialogVisible: false, //
         editDialogVisible: false, //
         picture: [],
         details: [],
         fileList: [],
         cooparationId: '',
 shopData: {
   name: '',
          token: '',
          picture: [],
          details: ''
 },
        form2: {
          name: '',
          addSwiper: '',
          addDetail: '',
		  coorparation: ''
        },
         addShopRules: {
             name: [{required: true, message: '', trigger: 'blur' }],
			 coorparation:  [{required: true, message: '', trigger: 'blur' }]
         },
         editForm: {
           name: '',
           picture: [],
           details: '',
           partnershipId: ''
         },
 params2:{},
        productList: [],
        totalElements: 0,
        number: 1,
        size: 10,
        page: 1,
        content: [],
        searchContent: [],
        searchNumber: 1,
        searchSize: 10,
        searchPage: 1,
        searchTotalElements: 0,
         picture: '',
         details: '',
         detailFile: [],
         pictureFile: [],
         reuploadDetail: '', //
         reuplodPicture: [],
         goodsId: '' ,// id,
         delPic: []
      }
     },
    methods: {
      switchShow() {
        let param = new URLSearchParams();
        param.append('token', this.token);
        axios({
            method: "post",
            url: '/lightspace/disPlayBuyer',
            data: param
        }).then((res) => {
         // console.log(res)
          if(this.showFlag =='') {
            this.showFlag = ''
          }else {
            this.showFlag =''
          }

        }).catch((err) => {
          console.log(err)
        })
      },
      // 
      handleChange(val) {
        this.form2.coorparation = val;
      },
      // 
      handleEditChange(val) {
        this.editForm.partnershipId = val;
      },
      handleRemove(file, fileList) {
        if(this.reuplodPicture.length) {
          let temp = this.reuplodPicture;
          let arr = temp.filter((item) => {
            if(item.uid !== file.uid) {
              return item
            }
          })
           this.reuplodPicture = arr;
        }else {
          this.delPic.push(file.url)
        }
      },
      handleRemoveDetail(file, fileList) {
        if(this.reuploadDetail.length == 0) {
          this.$message({
          message: '',
          type: 'warning'
        });
        return;
        }
      },
      //
      queryShop() {
        if(this.query == "") {
            this.getShopList(1);
           return;
        }
          let param = new URLSearchParams();
          param.append('token', this.token);
          param.append('name', this.query);
          param.append('page', this.searchPage);
          axios({
              method: "post",
              url: '/lightspace/findProductByName',
              data: param
          }).then(this.handleQuerySucc.bind(this))
          .catch(this.handleQueryErr.bind(this))
      },
      handleQuerySucc(res) {
        if(res.data.status === 10204) {
            this.$message.error(res.data.msg);
            this.$router.push('/login');
           } else if(res.data.status == 10208) {
            this.$message.error(res.data.msg);
         }else if(res.status == 200) {
            this.$message.success('');
            // console.log(res)
            res? res = res.data.data: '';
            this.searchContent = res.content;
            this.searchTotalElements = res.totalElements;
            this.searchSize = res.size;
            this.searchNumber = res.number + 1;
          }
      },
      handleQueryErr(err) {
          console.log(err)
      },
      //
      handleCurrentChange(val) {
         this.page = val;
         this.getShopList(val)
      },
      //
      handleSearchCurrentChange(val) {
         this.searchPage = val;
         this.queryShop();
      },
      //
      getShopList(page) {
        let param = new FormData();
        param.append('token', this.token);
        param.append('page', page)
        axios({
          method: 'post',
          data: param,
          url: '/lightspace/productList'
        }).then((res) => {
           // console.log(res)
          res? res = res.data.data: '';
          this.content = res.content;
          this.totalElements = res.totalElements;
          this.size = res.size;
          this.number = res.number + 1;
        }).catch((err) => {
          console.log(err)
        })
      },
      failResult(err){
        console.log(err)
      },
 
     beforeUpload(file){
      this.fileList.push(file);
 },
    handleUpload(raw){
      this.fileList.push(raw.file);
    },
    handleDetailUpload(raw) {
      this.details = raw.file;
    },
  // 
  reupload(raw) {
    this.reuploadDetail = raw.file;
  },
  // 
  reuploadPicture(raw) {
    this.reuplodPicture.push(raw.file)
  },
    async onresetSubmit() {
      let param = new FormData();
      param.append('token', this.token);
      param.append('id', this.goodsId);
      param.append('name', this.editForm.name);
      param.append('details', this.reuploadDetail);
      param.append('delPic', this.delPic);
      param.append('partnershipId', this.editForm.partnershipId)
      this.reuplodPicture.forEach(function (file) {
       param.append('picture', file); // 
            //Object
       })
      axios({
        method: 'post',
        data: param,
        url: '/lightspace/saveProduct'
      }).then((res) => {
        if(res.data.status == 200) {
          this.editDialogVisible = false;
          this.editForm.name = '';
          this.reuplodPicture = [];  //
          this.reuploadDetail = [];
          this.pictureFile = [];
          this.detailFile = [];
          this.delPic = [];
          this.getShopList(1);
        }
      }).catch((err) => {
        console.log(err)
      })
    },
     //
 async onSubmit(){//
         this.$refs.addShopFormRef.validate((valid) => {
         if(!valid) return;
         this.$refs.upload.submit();
         this.$refs.detailUpload.submit();
         let param = new FormData();
         param.append('token', this.token);
         param.append('name', this.form2.name);
         param.append('details', this.details);
         param.append('partnership', this.form2.coorparation)
         this.fileList.forEach(function (file) {
          param.append('picture', file); // 
               //Object
          })
         axios({
           method: 'post',
           data: param,
           url: '/lightspace/addProduct'
         }).then((res) => {
           // console.log(res)
           if(res.data.status == 200) {
             this.addDialogVisible = false;
             this.form2.name = '';
             this.$refs.upload.clearFiles(); // 
             this.$refs.detailUpload.clearFiles();
             this.getShopList(1);
           }
         }).catch((err) => {
           console.log(err)
         })
      })
    },
    //
     handleClose() {
      this.addDialogVisible = false;
      this.form2.name = '';
      this.$refs.upload.clearFiles();
      this.$refs.detailUpload.clearFiles();
     },
     handleEditClose() {
       this.detailFile = [];
       this.pictureFile = [];
       this.editDialogVisible = false;
       this.editForm.name= '';
     },
     //
     async removeShopById(id) {
       const confirmResult = await this.$confirm(', ?', '', {
       confirmButtonText: '',
       cancelButtonText: '',
       type: 'warning'
       }).catch(err => err)
       if(confirmResult !== 'confirm') {
           return this.$message.info('')
       }
       let param = new URLSearchParams();
       param.append('token', this.token);
       param.append('id', id)
       axios({
           method: 'post',
           url: '/lightspace/deleteProduct',
           data: param
       }).then(this.handleDeleteProSucc.bind(this))
       .catch(this.handleDeleteProErr.bind(this))
     },
     handleDeleteProSucc(res) {
         if(res.data.status === 10204) {
             this.$message.error(res.data.msg);
             this.$router.push('/login');
         } else if(res.data.status == 200) {
            this.$message.success('');
            if(this.query == '') {
                this.getShopList(1);
            }else {
              this.queryShop();
            }
         }
     },
     handleDeleteProErr(err) {
         console.log(err)
     },
     showEditDialog(id){
       this.goodsId = id;
       let param = new FormData();
       param.append('token', this.token);
       param.append('id', id);
       axios({
         method: 'post',
         data: param,
         url: '/lightspace/editProduct'
       }).then((res) => {
         this.editDialogVisible = true;
         this.editForm = res.data.data;
         let detailFile = res.data.data.details;
         let pictureFile = res.data.data.pictures;
         this.pictureFile = pictureFile.map((t) => {
           var obj = {};
           obj.url = t;
           return obj;
         })
         this.detailFile.push({
           url: detailFile
         });
       }).catch((err) => {
         console.log(err)
       })
     },
	 //
	 getOPtions() {
	     let param = new URLSearchParams();
	     param.append('token', this.token);
	     axios({
	         method: 'post',
	         url: '/lightspace/partnershipList',
	         data: param
	     }).then(this.handleGetOptionSucc.bind(this)).catch(this.handleGetOptionErr.bind(this))
	 },
	 handleGetOptionSucc (res) {
     // console.log(res)
	   if(res.data.status === 10204) {
	       this.$message.error(res.data.msg);
	       this.$router.push('/login');
	   } else if(res.data.status == 200) {
	      let options = res.data.data;
	      options.forEach((item, index) => {
	        this.options.push({
	          value: item.id,
	          label: item.name
	        })
	      })
	   }
	 },
	 handleGetOptionErr(err) {
	     console.log(err)
	 }
    }
    }
</script>

<style>
  input[type="file"] {
          display: none;
      }
    .avatar-uploader .el-upload {
        border: 1px dashed #d9d9d9;
        border-radius: 6px;
        cursor: pointer;
        position: relative;
        overflow: hidden;
    }
    .avatar-uploader .el-upload:hover {
        border-color: #409EFF;
    }
    .avatar-uploader-icon {
        font-size: 28px;
        color: #8c939d;
        width: 178px;
        height: 178px;
        line-height: 178px;
        text-align: center;
    }
    .avatar {
        width: 178px;
        height: 178px;
        display: block;
    }
    .avatar-uploader {
      border: 1px dashed #d9d9d9;
      border-radius: 6px;
      cursor: pointer;
      width: 20rpx;
      height: 20rpx;
    }
  .swiperImg {
    width: auto;
    height: 50px !important;
    margin: 5px;
    display: inline-block;
  }
  .bgGrey {
    background: grey
  }
</style>



