<template>
    <div>
          <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <!--  -->
        <el-card>
             <el-row :gutter="20">
                 <el-col :span="6">
                     <el-input placeholder=""  v-model="query"  clearable @clear="searchRole">
                        <el-button slot="append" icon="el-icon-search"  @click="searchRole"></el-button>
                     </el-input>
                 </el-col>
                 <el-col :span="6">
                        <el-button type="primary" @click="addRoleDialogVisible = true"></el-button>
                 </el-col>
             </el-row>
           <!--  -->
            <el-table :data="roleList.slice((currentPage-1) * pageSize, currentPage * pageSize)" border  stripe style="width: 100%" v-show="!this.searchRoleList.length">
                <el-table-column type="expand">
                    <template slot-scope="scope">
                        <!-- {{scope.row}} -->
                        <el-row :class="['bdbottom', 'vcenter', i1 === 0 ? 'bdtop': '']" v-for="(item1, i1) in scope.row.children" :key="item1.id">
                            <!--  -->
                            <el-col :span="10">
                                <el-tag >
                                    {{item1.authName}}
                                </el-tag>
                                <i class="el-icon-caret-right"></i>
                            </el-col>
                            <!--  -->
                            <el-col :span="14" >
                                <el-row :class="['bdtop', 'vcenter', i2 === 0 ? 'bdtop': '']" v-for="(item2, i2) in item1.children" :key="item2.id">
                                    <el-col :span="10" >
                                        <el-tag type="success">
                                            {{item2.authName}}
                                        </el-tag>
                                    </el-col>
                                </el-row>
                            </el-col>
                        </el-row>
                    </template>
                </el-table-column>
                <el-table-column type="index"></el-table-column>
                <el-table-column label="" prop="roleName"></el-table-column>
                <el-table-column label="" prop="description"></el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">
                        <el-button type="primary" size="mini" icon="el-icon-edit"  @click="showEditDialog(scope.row.roleId)" ></el-button>
                    </template>
                </el-table-column>
                 <el-table-column label="">
                    <template slot-scope="scope">
                        <el-button type="danger"  size="mini" icon="el-icon-delete" @click="removeRoleById(scope.row.roleId)"></el-button>
                    </template>
                </el-table-column>
                  <el-table-column label="">
                    <template slot-scope="scope">
                        <el-button type="warning"  size="mini" icon="el-icon-setting" @click="showSetRightDialog(scope.row)"></el-button>
                    </template>
                </el-table-column>
            </el-table>
            <!--  -->
            <el-table :data="searchRoleList" border  stripe style="width: 100%" v-show="this.searchRoleList.length">
                <el-table-column type="expand">
                    <template slot-scope="scope">
                        <!-- {{scope.row}} -->
                        <el-row :class="['bdbottom', 'vcenter', i1 === 0 ? 'bdtop': '']" v-for="(item1, i1) in scope.row.children" :key="item1.id">
                            <!--  -->
                            <el-col :span="10">
                                <el-tag >
                                    {{item1.authName}}
                                </el-tag>
                                <i class="el-icon-caret-right"></i>
                            </el-col>
                            <!--  -->
                            <el-col :span="14" >
                                <el-row :class="['bdtop', 'vcenter', i2 === 0 ? 'bdtop': '']" v-for="(item2, i2) in item1.children" :key="item2.id">
                                    <el-col :span="10" >
                                        <el-tag type="success">
                                            {{item2.authName}}
                                        </el-tag>
                                    </el-col>
                                </el-row>
                            </el-col>
                        </el-row>
                    </template>
                </el-table-column>
                <el-table-column type="index"></el-table-column>
                <el-table-column label="" prop="roleName"></el-table-column>
                <el-table-column label="" prop="description"></el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">
                        <el-button type="primary" size="mini" icon="el-icon-edit"  @click="showEditDialog(scope.row.roleId)" ></el-button>
                    </template>
                </el-table-column>
                 <el-table-column label="">
                    <template slot-scope="scope">
                        <el-button type="danger"  size="mini" icon="el-icon-delete" @click="removeRoleById(scope.row.roleId)"></el-button>
                    </template>
                </el-table-column>
                  <el-table-column label="">
                    <template slot-scope="scope">
                        <el-button type="warning"  size="mini" icon="el-icon-setting" @click="showSetRightDialog(scope.row)"></el-button>
                    </template>
                </el-table-column>
            </el-table>
            <!--  -->
            <el-pagination
                 v-show="!this.searchRoleList.length"
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                :page-sizes="[1, 2, 5, 10]"
                :page-size="pageSize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="roleList.length">
            </el-pagination>
        </el-card>
        <!--  -->
         <el-dialog title="" :visible.sync="addRoleDialogVisible" width="50%"  :before-close="handleClose">
            <el-form :model="addRoleForm"  :rules="addRoleRules" ref="roleFormRef" label-width="100px">
                 <el-form-item label="" prop="roleName">
                     <el-input v-model="addRoleForm.roleName" clearable></el-input>
                 </el-form-item>
                 <el-form-item label="" prop="description">
                     <el-input v-model="addRoleForm.description" clearable></el-input>
                 </el-form-item>
            </el-form>
             <span slot="footer" class="dialog-footer">
                 <el-button @click="handleClose"></el-button>
                 <el-button type="primary" @click=" submitRole" > </el-button>
             </span>
         </el-dialog>
       <!--  -->
        <el-dialog title="" :visible.sync="editRoleDialogVisible" width="50%">
           <el-form :model="editRoleForm" :rules="editRoleRules" ref="editroleFormRef" label-width="100px">
                <el-form-item label="" prop="roleName">
                    <el-input v-model="editRoleForm.roleName" clearable></el-input>
                </el-form-item>
                <el-form-item label="" prop="description">
                    <el-input v-model="editRoleForm.description" clearable></el-input>
                </el-form-item>
           </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="editRoleDialogVisible = false"> </el-button>
                <el-button type="primary" @click="editRoleInfo" > </el-button>
            </span>
        </el-dialog>
        <!--  -->
        <el-dialog title="" :visible.sync="settingDialogVisible" width="50%" @close="setRightDialogClose">
            <el-tree  ref="treeRef" :data="rightList" :props="defaultProps"  node-key="id" show-checkbox default-expand-all :default-checked-keys="defkeys"></el-tree>
            <span slot="footer" class="dialog-footer">
                <el-button @click="settingDialogVisible = false"> </el-button>
                <el-button type="primary" @click="settiingSave" > </el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
import axios from 'axios'
export default {
    created() {
        this.token = window.sessionStorage.getItem('token');
        this.getRoleList();
    },
   data() {
       return {
           token: '',
           addRoleDialogVisible: false,
           editRoleDialogVisible: false,
           settingDialogVisible: false,
           query: '',
           searchRoleList: [],
           addRoleForm: {
               roleName: '',
               description: ''
           },
           addRoleRules: {
                roleName: [{required: true, message: '', trigger: 'blur' }],
           },
           editRoleForm: {
                roleName: '',
                description: '',
                id: ''
           },
           editRoleRules: {
                roleName: [{required: true, message: '', trigger: 'blur' }]
           },
           roleList: [],
           currentPage: 1,
           pageSize: 5,
           total: 0,
           rightList: [],
           defaultProps: {
               children: 'children',
               label: 'authName'
           },
           // id
           defkeys: [],
           role: '', //
           roleId: '' //Id

       }
   },
   methods: {
     searchRole() {
        if(this.query == "") {
           this.getRoleList();
           this.searchRoleList = [];
           return;
        }
          let param = new URLSearchParams();
          param.append('token', this.token);
          param.append('name', this.query);
          axios({
              method: "post",
              url: '/lightspace/queryRole',
              data: param
          }).then(this.handleQuerySucc.bind(this))
          .catch(this.handleQueryErr.bind(this))
      },
     handleQuerySucc(res) {
       if(res.data.status === 10204) {
           this.$message.error(res.data.msg);
           this.$router.push('/login');
       } else if(res.data.status == 10212) {
           this.$message.error(res.data.msg);
           this.getRoleList();
           this.searchRoleList = [];
        }else if(res.data.status == 200) {
           this.$message.success('');
           this.searchRoleList = res.data.data;
         }
     },
      handleQueryErr(err) {
          console.log(err)
      },
     handleClose() {
      this.addRoleDialogVisible = false;
      this.$refs.roleFormRef.resetFields();
      this.addRoleForm.description = ''
     },
        resetAdd() {
          this.$refs.roleFormRef.resetFields();
          this.addRoleDialogVisible = false;
        },
        //
        submitRole() {
           this.$refs.roleFormRef.validate((valid) => {
               if(!valid) return;
               let param = new URLSearchParams();
               param.append('roleName', this.addRoleForm.roleName);
               param.append('description', this.addRoleForm.description);
               param.append('token', this.token)
               axios({
                   method: 'post',
                   url: '/lightspace/addRole',
                   data: param
               }).then(this.handleAddRoleSucc.bind(this))
               .catch(this.handleAddRoleErr.bind(this))
           })
        },
        handleAddRoleSucc(res) {
           if(res.data.status === 10204) {
               this.$message.error(res.data.msg);
               this.$router.push('/login');
           } else if(res.data.status == 200) {
              this.addRoleDialogVisible = false;
              this.$message.success('');
              this.$refs.roleFormRef.resetFields();
              this.getRoleList();
           }
        },
        handleAddRoleErr(err) {
            console.log(err)
        },
        //
        getRoleList() {
             let param = new URLSearchParams();
             param.append('token', this.token);
             axios({
                 method: 'post',
                 url: '/lightspace/roleList',
                 data: param
             }).then(this.handleGetRoleSucc.bind(this))
               .catch(this.handleGetRoleErr.bind(this))
        },
        handleGetRoleSucc(res) {
            if(res.data.status === 10204) {
                this.$message.error(res.data.msg);
                this.$router.push('/login');
            } else if(res.data.status == 200) {
               this.roleList = res.data.data;
            }

        },
        handleGetRoleErr(error) {
           console.log(error)
        },
         //pageSize
        handleSizeChange(newSize) {
            this.pageSize = newSize
            this.getRoleList();
        },
        //
        handleCurrentChange(val) {
           this.currentPage = val;
        },
        //
        showEditDialog (roleId) {
            this.editRoleDialogVisible = true;
            this.editRoleForm.id = roleId;
           let param = new URLSearchParams();
            param.append('roleId', roleId);
            param.append('token',this.token)
            axios({
                method: 'post',
                url: '/lightspace/editRole',
                data: param
            }).then(this.handleEditRoleSucc.bind(this))
            .catch(this.handleEditRoleErr.bind(this))
        },
        handleEditRoleSucc(res) {
           if(res.data.status === 10204) {
               this.$message.error(res.data.msg);
               this.$router.push('/login');
           } else if(res.data.status == 200) {
              this.editRoleForm = res.data.data
           }
        },
        handleEditRoleErr(err) {
            console.log(err)
        },
        editRoleInfo() {
            this.$refs.editroleFormRef.validate((valid) => {
                if(!valid)  return this.$message.error('');
                let param = new URLSearchParams();
                param.append('token', this.token);
                param.append('roleName', this.editRoleForm.roleName);
                param.append('description', this.editRoleForm.description);
                param.append('id', this.editRoleForm.id)
                axios({
                    method: 'post',
                    url: '/lightspace/saveRole',
                    data: param
                }).then(this.handleEditSaveSchoolSucc.bind(this))
                .catch(this.handleEditSaveSchoolErr.bind(this))
                })
        },
        //
        handleEditSaveSchoolSucc(res) {
             if(res.data.status === 10204) {
                 this.$message.error(res.data.msg);
                 this.$router.push('/login');
             } else if(res.data.status == 200) {
               //
                 this.schoolList = res.data.data;
                //
               this.editRoleDialogVisible = false;
                //
                this.$message.success('');
                this.getRoleList();
             }
        },
        handleEditSaveSchoolErr(err) {
            console.log(err)
        },
        //id
        async removeRoleById(id) {
            const confirmResult = await this.$confirm(', ?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
            }).catch(err => err)
            if(confirmResult !== 'confirm') {
                return this.$message.info('')
            }
            let param = new URLSearchParams();
            param.append('token', this.token);
            param.append('roleId', id)
            axios({
                method: 'post',
                url: '/lightspace/deleteRole',
                data: param
            }).then(this.handleDeleteRoleSucc.bind(this))
            .catch(this.handleDeleteRoleErr.bind(this))
            },
        handleDeleteRoleSucc(res) {
            if(res.data.status === 10204) {
                this.$message.error(res.data.msg);
                this.$router.push('/login');
            } else if(res.data.status == 200) {
               this.$message.success('');
               this.roleList = res.data.data;
               const totalPage = Math.ceil(this.roleList.length / this.pageSize) // 
               this.currentPage = this.currentPage > totalPage ? totalPage : this.currentPage
               this.currentPage = this.currentPage < 1 ? 1 : this.currentPage
            }
            this.getRoleList();
        },
        handleDeleteRoleErr(err) {
            console.log(err)
        },
        //
        showSetRightDialog(role) {
            this.roleId = role.roleId;
            this.role= role;
            let param = new URLSearchParams();
            param.append('token', this.token)
            //
            axios({
                method: 'post',
                url: '/lightspace/menuList',
                data: param
            }).then(this.handleGetMenuListSucc.bind(this))
            .catch(this.getMenuListErr.bind(this))
        },
        handleGetMenuListSucc(res) {
           if(res.data.status === 10204) {
               this.$message.error(res.data.msg);
               this.$router.push('/login');
           } else if(res.data.status == 200) {
              this.rightList = res.data.data;
              this.getLeafKeys(this.role,this.defkeys);   //id
              this.settingDialogVisible = true;
           }
        },
        getMenuListErr(err) {
            console.log(err)
        },
        //id,
        getLeafKeys(node, arr){
            if(!node.children) {
                return arr.push(node.id)
            }
            node.children.forEach(item =>
            this.getLeafKeys(item, arr))
        },
        //
        setRightDialogClose() {
            this.defkeys = [];
        },
        //
        settiingSave() {
            const keys = [...this.$refs.treeRef.getCheckedKeys(), ...this.$refs.treeRef.getHalfCheckedKeys()];
            const idStr = keys.join('-');
            let param = new URLSearchParams();
            param.append('token' , this.token);
            param.append('roleId', this.roleId);
            param.append('rightId', idStr);
            axios({
                method: 'post',
                data: param,
                url: '/lightspace/roleRight'

            }).then(this.handleSaveRightSucc.bind(this))
            .catch(this.handleSaveRightErr.bind(this))

        },
        handleSaveRightSucc(res) {
          if(res.data.status === 10204) {
              this.$message.error(res.data.msg);
              this.$router.push('/lightspace/login');
          } else if(res.data.status == 200) {
             this.settingDialogVisible = false;
             this.getRoleList();
          }
           
        },
        handleSaveRightErr(err) {
            console.log(err)
        }
        }
}
</script>
<style lang="less" scoped>
.vcenter {
    display: flex;
    align-items: center;
}
</style>



