<template>
  <div>
     <!--  -->
      <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
      <!--  -->
      <el-card>
        <el-table :data="screenTempList" border  stripe style="width: 100%"   @row-click="chooseone">
          <el-table-column type="index"></el-table-column>
          <el-table-column label="" prop="name"></el-table-column>
          <el-table-column label="" prop="first"></el-table-column>
          <el-table-column label="" prop="remark"></el-table-column>
          <el-table-column label=" url" prop="url"></el-table-column>
          <el-table-column label="">
              <template slot-scope="scope">
                 <el-button type="primary" size="middle" icon="el-icon-edit"  @click="showEditDialog(scope.row.id)" ></el-button>
              </template>
          </el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
               <el-button type="success" size="middle"  v-if="scope.row.selected == 1"></el-button>
               <el-button type="primary" size="middle"  v-if="scope.row.selected == 0"></el-button>
            </template>
          </el-table-column>
          <!-- <el-table-column label="">
              <template slot-scope="scope">
                <el-radio  v-model="radio" label="1" v-if="scope.row.selected == 1"> {{scope.row.selected}}</el-radio>
                <el-radio  v-model="radio"  label="0" v-else="scope.row.selected == 0"></el-radio>
              </template>
          </el-table-column> -->
        </el-table>
      </el-card>
      <el-dialog title="" :visible.sync="editDialogVisible" width="50%" @close="editDialogClosed">
          <el-form :model="editScreenForm" :rules="editScreenRules" ref="editScreenRef" label-width="100px">
              <el-form-item label="" prop="name">
                  <el-input v-model="editScreenForm.name"></el-input>
              </el-form-item>
              <el-form-item label="" prop="first">
                  <el-input v-model="editScreenForm.first"></el-input>
              </el-form-item>
              <el-form-item label="" prop="remark">
                  <el-input v-model="editScreenForm.remark"></el-input>
              </el-form-item>
              <el-form-item label="" prop="url">
                  <el-input v-model="editScreenForm.url"></el-input>
              </el-form-item>
              <el-form-item label="" prop="selected">
                <el-radio v-model="editScreenForm.selected" :label="1"></el-radio>
                <el-radio v-model="editScreenForm.selected" :label="0"></el-radio>
              </el-form-item>
          </el-form>
          <span slot="footer" class="dialog-footer">
              <el-button @click="editDialogVisible = false"> </el-button>
              <el-button  type="primary" @click="editScreenInfo"> </el-button>
          </span>
      </el-dialog>
    </div>
</template>
<script>
  import axios from 'axios'
  export default {
    created() {
      this.token = window.sessionStorage.getItem('token');
      this.getscreenTempList();
    },
    data() {
      return {
        token: '',
        radio: '',
        id: '',
        screenTempList: [],
        editDialogVisible: false,
        editScreenForm: {
            name: '',
            first:'',
            remark: '',
            url: '',
            selected: ''
        },
        editScreenRules: {
             name: [{required: true, message: '', trigger: 'blur' }],
             first: [{required: true, message: '', trigger: 'blur' }],
             remark: [{required: true, message: '', trigger: 'blur' }],
             url: [{required: false, message: '', trigger: 'blur' }],
             selected:[{required: true, message: '', trigger: 'blur' }]
        }
      }
    },
    methods: {
      chooseone(row){
        this.radio = row.id
      },
      getscreenTempList() {
        let param = new FormData();
        param.append('token', this.token);
        param.append('type', 'screening');
        axios({
          method: 'post',
          url: '/lightspace/msgTempList',
          data: param
        }).then(this.handleGetScreenTemSucc.bind(this))
        .catch(this.handleGEetScreenTemErr.bind(this))
      },
      handleGetScreenTemSucc(res) {
        if(res.data.status == 200) {
          this.screenTempList = res.data.data;
        }
      },
      handleGEetScreenTemErr(err) {
        console.log(err)
      },
      showEditDialog(id) {
        this.editDialogVisible = true;
        this.id = id;
        let param = new FormData();
        param.append('id', id);
        param.append('token',this.token)
        axios({
            method: 'post',
            url: '/lightspace/editMsgTemp',
            data: param
        }).then(this.handleEditScreenSucc.bind(this))
        .catch(this.handleEditScreenErr.bind(this))
        },
        handleEditScreenSucc(res) {
          if(res.data.status === 10204) {
              this.$message.error(res.data.msg);
              this.$router.push('/login');
             } else if(res.data.status == 200) {
              this.editScreenForm = res.data.data;
            }
        },
        handleEditScreenErr(err) {
            console.log(err)
        },
      //
      editDialogClosed() {
          this.$refs.editScreenRef.resetFields()
      },
      //
      editScreenInfo() {
        this.$refs.editScreenRef.validate((valid) => {
        if(!valid)  return;
        let param = new FormData();
        param.append('token', this.token);
        param.append('id', this.id);
        param.append('name', this.editScreenForm.name);
        param.append('first', this.editScreenForm.first);
        param.append('remark', this.editScreenForm.remark);
        param.append('url', this.editScreenForm.url);
        param.append('selected', this.editScreenForm.selected)

        axios({
            method: 'post',
            url: '/lightspace/saveMsgTemp',
            data: param
        }).then(this.handleEditSaveScreenSucc.bind(this))
        .catch(this.handleEditSaveScreenErr.bind(this))
        })
      },
       handleEditSaveScreenSucc(res) {
       if(res.data.status === 10204) {
           this.$message.error(res.data.msg);
           this.$router.push('/login');
          }else if(res.data.status == 200) {
            //
             this.screenList = res.data.data;
            //
            this.editDialogVisible = false;
            //
            this.$message.success('');
            this.getscreenTempList()
          }
      },
      handleEditSaveScreenErr(err) {
         console.log(err)
      },
    }
  }
</script>

<style>
</style>



